<template>
  <div class="text">
    <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 20 }" v-model="text"></el-input>
    <el-button class="button" @click="handle">handle</el-button>
    <el-button class="button" @click="handleMerged">handleMerged</el-button>
    <el-button class="button" @click="handle3Cols">handle3Cols</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      text: ''
    }
  },
  methods: {
    convert(arr) {
      let obj = arr.reduce((acc, cur) => {
        return { ...acc, [cur[0]]: acc[cur[0]] ? [...acc[cur[0]], cur[1]] : [cur[1]] }
      }, {})
      let res = Object.entries(obj).reduce((acc, [name, list]) => {
        return [...acc, { name, list: list.filter(e => e) }]
      }, [])
      return res
    },
    handle() {
      console.log(this.text)
      let arr = this.text.split(/\n/).map(e => e.split(/\s+/))
      let res = convert(arr)
      console.log(arr, res)
    },
    handleMerged() {
      console.log('merged', this.text)
      let name = ''
      let arr = this.text.split(/\n/).map(e => {
        e = e.split(/\s+/)
        if (e[0].trim()) name = e[0]
        else e[0] = name
        return e
      })
      let res = convert(arr)
      console.log(arr, res)
    },
    handle3Cols() {
      console.log('3 cols', this.text)
      let name = ''
      let arr = this.text.split(/\n/).map(e => {
        e = e.split(/\s+/)
        if (e[0].trim()) name = e[0]
        else e[0] = name
        return e
      })
      let obj = arr.reduce((acc, cur) => {
        let appends = cur[2] === '标签和要素' ? ['（标签）', '（要素）'] : ['']
        let arr = appends.map(append => cur[1] + append)
        return { ...acc, [cur[0]]: acc[cur[0]] ? [...acc[cur[0]], ...arr] : arr }
      }, {})
      let res = Object.entries(obj).map(([key, value]) => {
        console.log({ value })
        return { value: key, children: value.map(e => ({ value: e })) }
      })
      console.log(arr, obj, res)
    }
  }
}
</script>

<style lang="scss" scoped>
.text {
  margin: 0 auto;
  padding: 20px;
  width: 400px;
  height: 100%;
}
.button {
  margin-top: 20px;
}
</style>
