<template>
  <div class="css">
    <div>{{ percent | toFixed }}%</div>
    <div class="progress" ref="progress"></div>
    <el-button @click="setProgressInterval">setProgressInterval</el-button>
  </div>
</template>

<script>
export default {
  filters: {
    toFixed(n) {
      return n === 0 || n === 100 ? n : n.toFixed(1)
    }
  },
  data() {
    return {
      percent: 0
    }
  },
  mounted() {
    this.width = this.$refs.progress.clientWidth
    this.height = this.$refs.progress.clientHeight
    this.minus = this.height / 2 / this.$refs.progress.clientWidth * 100
    this.setProgress(0)
  },
  methods: {
    setProgress(percent = 0) {
      this.percent = percent
      this.$refs.progress.style.setProperty('--percent', (percent - this.minus) + '%')
    },
    setProgressInterval() {
      for (let i = 0; i <= 1000; i++) {
        setTimeout(() => {
          this.setProgress(i / 10)
        }, 50 * i)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.css {
  padding: 30px;
}
.progress {
  --percent: 0;
  --color: red;
  margin: 10px auto;
  width: 600px;
  height: 12px;
  border-radius: 6px;
  background: radial-gradient(circle 6px at var(--percent), var(--color), var(--color) 100%, transparent), linear-gradient(to right, var(--color) var(--percent), #eee var(--percent));
}
</style>
