<template>
  <div class="house">
    <div v-html="html"></div>
  </div>
</template>

<script>
import showdown from 'showdown'
import { markdown } from '../data/house.js'
let converter = new showdown.Converter()

export default {
  name: 'house',
  data() {
    return {
      html: '<div>123</div>'
    }
  },
  mounted() {
    this.html = converter.makeHtml(markdown)
  }
}
</script>

